body{font-family:Raleway, sans-serif;margin:0;font-size:1.5em;text-align:center;background:#1b1523;color:#fff}.App{max-width:860px;margin:40px auto}button{background:none;border:2px solid #fff;padding:6px 12px;border-radius:4px;color:#fff;font-weight:bold;cursor:pointer;font-size:1em}button:hover{background:#c23866;color:#fff}.card-grid{margin-top:40px;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:20px;gap:20px}

body{font-family:Raleway, sans-serif;margin:0;font-size:1.5em;text-align:center;background:#1b1523;color:#fff}.card{position:relative}.card .front{-webkit-transform:rotateY(90deg);transform:rotateY(90deg);position:absolute;transition:all ease-in 0.2s}.card .cover{transition:all ease-in 0.2s;transition-delay:0.2s}.card img{width:100%;display:block;border:2px solid #fff;border-radius:6px}.flipped .front{-webkit-transform:rotateY(0deg);transform:rotateY(0deg);transition-delay:0.2s}.flipped .cover{-webkit-transform:rotateY(90deg);transform:rotateY(90deg);transition-delay:0s}

